import React from 'react';
import Carousel from 'nuka-carousel';
import ChildMob from './carouselChildren/carouselChildMob';
import CarouselBottomControls from './carouselBottomControls/carouselBottomControls';
import teamList from './makingArrForMob';

const Slider = () => (
  <Carousel
    initialSlideHeight={330}
    renderTopCenterControls={null}
    renderCenterLeftControls={null}
    renderCenterRightControls={null}
    renderBottomCenterControls={obj => <CarouselBottomControls obj={obj} />}
  >
    {teamList.map(list => (
      <ChildMob key={list[0].id} list={list} />
    ))}
  </Carousel>
);

export default Slider;
